<template>
    <div class="orderPayment">
        <loading :active.sync="isLoading"></loading>
        <div class="container">
            <!--  -->
            <div class="row text-center p-5">
                <div class="col-md-3 alert alert-sborange alert-rounded">
                    <i class="fas fa-check-circle"></i>
                    
                    <i class="fas fa-angle-double-right"></i>
                </div>
                <div class="col-md-3 alert alert-sborange alert-rounded">
                    
                    <i class="fas fa-angle-double-right"></i>
                </div>
                <div class="col-md-3 alert alert-sbtan alert-rounded" role="alert"></div>
                <div class="col-md-3 alert alert-sbtan alert-rounded"></div>
            </div>

            <div class="row justify-content-center mt-4 pb-5">
                <div class="col-md-10">
                    <form class="border border-sbtan rounded py-4 px-5" @submit.prevent="creatrOrder">  <!-- submit -->
                        <div class="h4"></div>
                        <div class="form-group">
                            <label for="name"></label>
                            <input type="text" class="form-control" name="name" id="username" v-model="form.user.name" v-validate="'required'" placeholder=""
                            :class="{'is-invalid' : errors.has('name')}">
                            <span class="text-danger" v-if="errors.has('name')">{{ errors.first('name') }}</span>  <!-- false -->
                        </div>

                        <div class="form-row">
                            <div class="col-md-6">
                                <label for="tel"></label>
                                <input type="tel" class="form-control" name="tel" id="usertel" v-model="form.user.tel" v-validate="'required'" placeholder=""
                                :class="{'is-invalid' : errors.has('tel')}">
                                <span class="text-danger" v-if="errors.has('tel')">{{ errors.first('tel') }}</span>  <!-- false -->
                            </div>
                            <div class="col-md-6">
                                <label for="payment"></label>
                                <select name="payment" id="payment" class="form-control" v-model="form.user.payment">
                                    <option value="selected"></option>
                                    <option value="COD"></option>
                                    <option value="CVS"></option>
                                    <option value="ATM">ATM</option>
                                </select>
                                <span class="text-danger" v-if="errors.has('payment')">{{ errors.first('payment') }}</span>
                            </div>
                        </div>
                            
                        <div class="form-group">
                            <label for="useraddress"></label>
                            <div class="form-group mt-3">
                                <input type="address" class="form-control" name="address" id="useraddress" v-model="form.user.address" v-validate="'required'" placeholder=""
                                :class="{'is-invalid': errors.has('address')}">
                                <span class="text-danger" v-if="errors.has('address')">{{ errors.first('address') }}</span>  <!-- false -->
                            </div>
                        </div>
                        <div class="form-group">
                            <label for="userEmail">E-mail</label>
                            <input type="email" class="form-control" name="email" id="useremail" v-model="form.user.email" v-validate="'required|email'" placeholder="">
                            <span class="text-danger" v-if="errors.has('email')">{{ errors.first('email') }}</span>  <!-- false -->
                        </div>
                        <div class="form-group">
                            <label for="message"></label>
                            <textarea class="form-control" name="email" id="message" rows="3"></textarea>
                        </div>
                        
                        <!--  -->
                        <div class="form-group notice" v-if="form.user.payment === 'COD'">
                            <h5 class="text-sborange">PAYMENT
                                <i class="fas fa-truck"></i> | 
                            </h5>
                            <ul class="text-sbbrown">
                                <li></li>
                                <li>()</li>
                            </ul>
                        </div>
                        <div class="form-group notice" v-if="form.user.payment === 'CVS'">
                            <h5 class="text-sborange">PAYMENT
                                <i class="fas fa-store-alt"></i> | 
                            </h5>
                            <ul class="text-sbbrown">
                                <li>7-11OK</li>
                                <li></li>
                                <li>24</li>
                            </ul>
                        </div>
                        <div class="form-group notice" v-if="form.user.payment === 'ATM'">
                            <h5 class="text-sborange">PAYMENT
                                <i class="fas fa-cash-register"></i> | ATM
                            </h5>
                            <ul class="text-sbbrown">
                                <li></li>
                                <li>24</li>
                            </ul>
                        </div>
                        <div class="text-right mt-5">
                            <button type="submit" class="btn btn-secondary" @click.prevent="goBack"></button>
                            <button type="submit" class="btn btn-sborange ml-2"></button>
                        </div>


                    </form>

                    
                </div>
            </div>
        </div>

        <!-- Modal -->
        <div class="modal fade" id="leaveModal" tabindex="-1" role="dialog" aria-hidden="true" data-backdrop="static">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                    <div class="modal-body">
                        <p></p>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-secondary cancel" data-dismiss="modal"></button>
                        <button type="button" class="btn btn-sborange confirm" data-dismiss="modal"></button>
                    </div>
                </div>
            </div>
        </div>


    </div>
</template>





<script>
import $ from 'jquery';

export default {
    data() {
        return {
            form: {
                user: {
                    name: '',
                    email: '',
                    rel: '',
                    address: '',
                    payment: 'selected',
                },
                message: '',
            },
            isLoading: false, // loading 
            isNext: false, // 
            cartData: {}, // 
            isConfirm: false, // API
        }
    },
    methods: {
        creatrOrder() {
            // 
            const vm = this;
            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order`;
            const order = vm.form;
            vm.isLoading = true;
            vm.isNext = true;
            // 
            vm.$validator.validate().then((valid) => {
                if (valid) {
                    vm.$http.post(url, { data: order }).then((response) => {
                        console.log('creatrOrder' , response.data);
                        if (response.data.success) {
                            // localStorage cart
                            localStorage.removeItem('cart');
                            vm.$bus.$emit('cart:get');
                            let orderId = response.data.orderId;
                            vm.$router.push(`/shopping/OrderInfo/${orderId}`);
                        } else {
                            vm.$bus.$emit('message:push', response.data.message, 'danger');
                            vm.$router.push('/shopping/productlist');
                        }
                    });
                } else {
                    vm.$bus.$emit('message:push', '', 'danger');
                }
                vm.isLoading = false;
            });
        },
        goBack() {
            this.isNext = true;
            this.$router.go(-1);
        },
        // 
        cancelCart() {
            const vm = this;
            const getUrl = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
            vm.$http.get(getUrl).then((response) => {
                if (!response.data.success) {
                    vm.$bus.$emit('message:push', response.data.message, 'danger');
                } else {
                    vm.cartData = response.data.data;
                    if (vm.cartData.carts.length > 0) {
                        vm.isConfirm = true;
                    } else {
                        vm.isConfirm = false;
                    }
                if (vm.isConfirm) {
                    vm.cartData.carts.forEach((item) => {
                        const delUurl = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${item.id}`;
                        vm.$http.delete(delUurl).then((res) => {
                            if (!res.data.success) {
                                vm.$bus.$emit('message:push', res.data.message, 'danger');
                            }
                        });
                    });
                }
                }
            });
        },
    },
    beforeRouteLeave(to, from, next) {
        const vm = this;
        if (vm.isNext) {
            next();
        } else {
            $('#leaveModal').modal('show');
            $('.confirm').on('click', () => {
                vm.cancelCart();
                next();
            });
            $('.cancel').on('click', () => {
                next(false);
            });
        }
    },
}



</script>





