<template>
    <div class="carList">
        <loading :active.sync="isLoading"></loading>
        <div class="container">
            <!--  -->
            <div class="row text-center p-5">
                <div class="col-md-3 alert alert-sborange alert-rounded">
                    
                    <i class="fas fa-angle-double-right"></i>
                </div>
                <div class="col-md-3 alert alert-sbtan alert-rounded"></div>
                <div class="col-md-3 alert alert-sbtan alert-rounded" role="alert"></div>
                <div class="col-md-3 alert alert-sbtan alert-rounded"></div>
            </div>
            <div class="row mt-4">
                <div class="col-md-8 table-responsive">
                    <table class="table table-hover">
                        <thead>
                            <tr class="text-sbbrown font-weight-bold">
                                <th width="8%"></th>
                                <th width="18%"></th>
                                <th></th>
                                <th width="15%" class="text-center"></th>
                                <th width="15%" class="text-center"></th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr class="text-sbbrown" v-for="item in cartData.carts" :key="item.id">
                                <td class="align-middle">
                                    <button type="button" class="btn btn-outline-sbtan border-0" @click.prevent="removeCartItem(item.id)">
                                        <i class="fas fa-trash-alt"></i>
                                    </button>
                                </td>
                                <td class="align-middle">
                                    <img :src="item.product.imageUrl" class="img-thumbnail">
                                </td>
                                <td class="align-middle" @click="$router.push(`/shopping/productdetail/${item.id}`)">{{ item.product.title }}</td>
                                <!-- <td class="align-middle text-right orderColumn" v-if="isConfirm">{{ item.qty }}</td> -->
                                <td class="align-middle text-right editColumn">{{ item.qty }}
                                    <!-- <div class="btn-group border countNumber">
                                        <button type="button" class="btn btn-light btn-sm rounded-0" @click="changeQty(item, -1)">
                                            <i class="fas fa-minus"></i>
                                        </button> 
                                        <input class="productNumber text-center" type="text" max='10' min='1' v-model="item.qty" @change="changeNumber(item, item.qty)">{{ item.qty }}
                                        <button type="button" class="btn btn-light btn-sm rounded-0" @click="changeQty(item, -1)">
                                            <i class="fas fa-plus"></i>
                                        </button>
                                    </div> -->
                                </td>
                                <td class="align-middle text-right">{{ item.total | currency }}</td>
                            </tr>
                        </tbody>
                        <tfoot>
                            <tr>
                                <td colspan="4" class="font-weight-bold text-right"></td>
                                <td class="h5 font-weight-bold text-right text-danger">{{ cartData.total | currency }}</td>
                            </tr>
                        </tfoot>
                    </table>
                </div>
                <div class="col-md-4 mb-6">
                    <div class="input-group">
                        <input type="text" class="form-control " v-model="coupon_code" placeholder=":aniyo">
                        <div class="input-group-append">
                        <button type="button" class="btn btn-sbtan" @click="addCouponCode"></button>
                        </div>
                    </div>
                    <div class="d-flex justify-content-between my-2 p-2">
                        <span></span>
                        <span>{{ cartData.total | currency }}</span>
                    </div>
                    <div class="d-flex justify-content-between mb-2 p-2">
                        <span></span>
                        <span class="justify-content-end">- {{ cartData.total - cartData.final_total | currency }}</span>
                    </div>
                    <p style="font-size:13px; color:#EA562A">{{ couponResponse }}</p>
                    <div class="d-flex justify-content-between mb-5 p-2">
                        <span></span>
                        <span>{{ cartData.final_total | currency }}</span>
                    </div>
                </div>
                
            </div>
            <div class="text-right mt-5 pb-5">
                <button type="button" class="btn btn-secondary mr-2" @click.prevent="$router.push('shopping/productlist')"></button>
                <button type="submit" class="btn btn-sborange" @click.prevent="goNextPage"></button>
            </div>
        </div>

        <!-- Modal -->
        <div class="modal fade" id="leaveModal" tabindex="-1" role="dialog" aria-hidden="true" data-backdrop="static">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                    <div class="modal-body">
                        <p></p>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-secondary cancel" data-dismiss="modal"></button>
                        <button type="button" class="btn btn-sborange confirm" data-dismiss="modal"></button>
                    </div>
                </div>
            </div>
        </div>


    </div>
</template>



<script>
import $ from 'jquery';

export default {
    data() {
        return {
            cart: {}, // (localStorage)
            cartLength: '', // (localStorage)
            coupon_code: '', // 
            // total: 0, // 
            cartData: {
                // total: 0,
                // final_total: 0,
            },
            couponResponse: '',
            isConfirm: false, // API
            isNext: false, // 
            isLoading: false,
        };
    },
    methods: {
        getCart() {  // localStorage
            const vm = this;
            vm.isLoading = true;
            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
            vm.$http.get(url).then(response => {
                vm.isLoading = false;
                vm.myCarts = response.data.data;
                console.log('getCart',response.data.data);
            });
        },
        removeCartItem(id) {
            const vm = this;
            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${id}`;
            vm.$http.delete(url).then(() => {
                // console.log('removeCartItem',response.data);
                vm.getCart();
                vm.$bus.$emit("updateBag");
                
            });
        },
        // 
        changeQty(product, num) {
            const qty = parseInt((product.qty + num), 10);
            this.changeNumber(product);
            console.log('', qty);
        },
    
        // 
        changeNumber(product, qty) {
            const vm = this;
            let productIndex = -1;
            if (vm.cart.length > 0) {
                vm.cart.forEach((item, index) => {
                    if (item.id === product.id) {
                        productIndex = index;
                    }
                });
            }
            if (qty >= 10) {
                vm.cart[productIndex].qty = 10;
            } else if (qty <= 1) {
                vm.cart[productIndex].qty = 1;
            } else {
                vm.cart[productIndex].qty = product.qty;
            }
            // 
            const total = parseInt((vm.cart[productIndex].price * vm.cart[productIndex].qty), 10);
            vm.cart[productIndex].total = total;
            // 
            // localStorage.setItem('cart', JSON.stringify(vm.cart));
            // vm.$bus.$emit('cart:get');
            vm.getCart();
        },
        // (API)
        getCartData() {
            const vm = this;
            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
            vm.$http.get(url).then((response) => {
                // console.log('getCartData' , response.data);
                vm.isLoading = false;
                vm.cartData = response.data.data;

                // if (!response.data.success) {
                // vm.$bus.$emit('message:push', response.data.message, 'danger');
                // } 
                // else {
                //     vm.cartData = response.data.data;
                //     if (vm.cartData.carts.length > 0) {
                //         vm.isConfirm = true;
                //     } else {
                //         vm.isConfirm = false;
                //     }
                // }
            });
        },
        // 
        addCouponCode() {
            const vm = this;
            let code = {
                code: vm.coupon_code,
            };
            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/coupon`;
            vm.$http.post(url, { data: code }).then(response => {
                if (response.data.success) {
                    vm.couponResponse = response.data.message;
                    vm.getCartData();   //  vm.getCartData();
                } else {
                    vm.couponResponse = response.data.message;
                }
            });
        },
        // 
        goNextPage() {
            this.isNext = true;
            this.$router.push('/shopping/OrderPayment');
        },
    },
    created() {
        const vm = this;
        vm.getCart();
        vm.getCartData();
        // vm.confirmCart();
        // vm.cancelCart();
        vm.$bus.$on('checkCart:get', () => vm.getCart());
    },
    beforeRouteLeave(to, from, next) {
        const vm = this;
        if (vm.isNext) {
            next();
        } else {
            $('#leaveModal').modal('show');
            $('.confirm').on('click', () => {
                vm.removeCartItem();
                next();
            });
            $('.cancel').on('click', () => {
                next(false);
            });
        }
    },
}
</script>




