// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import axios from 'axios' // ajax
import VueAxios from 'vue-axios' // vue
import 'bootstrap';  // bootstrap
import Loading from 'vue-loading-overlay';  // vue-loading
import 'vue-loading-overlay/dist/vue-loading.css';  // vue-loading css
import VeeValidate from 'vee-validate';  //  validate
import zhTWValidate from 'vee-validate/dist/locale/zh_TW';  // VeeValidate
import VueI18n from 'vue-i18n';  // i18n

import App from './App'
import router from './router';
import './bus';  // event.bus
import currencyFilter from './filters/currency';  // 
import dateFilter from './filters/date';

Vue.config.productionTip = false;

Vue.use(VueAxios, axios);  // axios
Vue.use(VeeValidate);  // VeeValidate
VeeValidate.Validator.localize('zhTW' , zhTWValidate);  // VeeValidate
// 
Vue.component('Loading' , Loading);  // 
//  filter 
Vue.filter('currency' , currencyFilter);  // ('' , )
Vue.filter('date', dateFilter);  // 

axios.defaults.withCredentials = true;  //  cookie 

Vue.use(VueI18n);

const i18n = new VueI18n({
  locale: 'zhTW',
});

Vue.use(VeeValidate, {
  events: 'input|blur',
  i18n,
  dictionary: {
    zhTWValidate,
  },
});


/* eslint-disable no-new */
new Vue({
  i18n,
  el: '#app',
  components: {App},
  template: '<App/>',
  router, // routernew vue
});


router.beforeEach((to, from, next) => {
  console.log('to' , to, 'from' , from, 'next' , next);
  // ...
  if (to.meta.requiresAuth) {  // requiresAuth
    const api = `${process.env.APIPATH}/api/user/check`;
    const vm = this;  // vm = data
    axios.post(api).then((response) => {  // vuethis.$httpaxios
      console.log(response.data);
      if (response.data.success) {  // 
        next();
      }else {
        next({
          path: '/login',
        });
      }
    });
  }else {  // requiresAuth
    next();
  }
});



