<template>
    <div>
        <loading :active.sync="isLoading"></loading>
        <div class="text-right">
            <button class="btn btn-info mt-4" @click="openModal(true)"></button>
        </div>
        <table class="table mt-4">
            <thead>
                <th width="120" class="text-center"></th>
                <th width="130" class="text-center"></th>
                <th></th>
                <th width="120" class="text-center"></th>
                <th width="120" class="text-center"></th>
                <th width="100" class="text-center"></th>
                <th width="130" class="text-center"></th>
            </thead>
            <tbody>
                <tr v-for="(item) in products" :key="item.id">
                    <td><img :src="item.imageUrl" width="80px" height="80px"></td>
                    <td class="text-center">{{ item.category }}</td>
                    <td>{{ item.title }}</td>
                    <td class="text-right">
                        {{ item.origin_price | currency }}
                    </td>
                    <td class="text-right">
                        {{ item.price | currency }}
                    </td>
                    <td class="text-center">
                        <span v-if="item.is_enabled" class="text-success"></span>
                        <span v-else></span>
                    </td>
                    <td>
                        <button class="btn btn-outline-info btn-sm" @click="openModal(false , item)"></button>
                        <button class="btn btn-outline-danger btn-sm" @click="delModal(item)"></button>
                    </td>
                </tr>
            </tbody>
        </table>
        <!--  -->
        <Pagination :pagination="pagination" @emitpage="getProducts"></Pagination>
        <!-- Modal -->
        <div class="modal fade" id="productModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog modal-lg" role="document">
                <div class="modal-content border-0">
                    <div class="modal-header bg-dark text-white">
                        <h5 class="modal-title" id="exampleModalLabel">
                            <span></span>
                        </h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div class="modal-body">
                        <div class="row">
                            <div class="col-sm-4">
                                <div class="form-group">
                                    <label for="image"></label>
                                    <input type="text" class="form-control" id="image" v-model="tempProduct.imageUrl" placeholder="">
                                </div>
                                <div class="form-group">
                                    <label for="customFile"> <i class="fas fa-circle-notch fa-spin" v-if="status.fileUploading"></i></label>
                                    <input type="file" id="customFile" class="form-control" ref="files" @change="uploadFile">
                                </div>
                                <img img="https://images.unsplash.com/photo-1483985988355-763728e1935b?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=828346ed697837ce808cae68d3ddc3cf&auto=format&fit=crop&w=1350&q=80" class="img-fluid" :src="tempProduct.imageUrl" alt="">
                            </div>
                            <div class="col-sm-8">
                                <div class="form-group">
                                    <label for="title"></label>
                                    <input type="text" class="form-control" id="title" v-model="tempProduct.title" placeholder="">
                                </div>

                                <div class="form-row">
                                    <div class="form-group col-md-6">
                                        <label for="category"></label>
                                        <input type="text" class="form-control" id="category" v-model="tempProduct.category" placeholder="">
                                    </div>
                                    <div class="form-group col-md-6">
                                        <label for="price"></label>
                                        <input type="unit" class="form-control" id="unit" v-model="tempProduct.unit" placeholder="">
                                    </div>
                                </div>

                                <div class="form-row">
                                    <div class="form-group col-md-6">
                                        <label for="origin_price"></label>
                                        <input type="number" class="form-control" id="origin_price" v-model="tempProduct.origin_price" placeholder="">
                                    </div>
                                    <div class="form-group col-md-6">
                                        <label for="price"></label>
                                        <input type="number" class="form-control" id="price" v-model="tempProduct.price" placeholder="">
                                    </div>
                                </div>
                                <hr>

                                <div class="form-group">
                                    <label for="description"></label>
                                    <textarea type="text" class="form-control" id="description" maxlength="80" v-model="tempProduct.description" placeholder=""></textarea>
                                </div>
                                <div class="form-group">
                                    <label for="content"></label>
                                    <textarea type="text" class="form-control" id="content" v-model="tempProduct.content" placeholder=""></textarea>
                                </div>
                                <div class="form-group">
                                    <div class="form-check">
                                        <input class="form-check-input" type="checkbox" v-model="tempProduct.is_enabled" :true-value="1" :false-value="0" id="is_enabled">
                                        <!-- 01:true-value="1" :false-value="0" -->
                                        <label class="form-check-label" for="is_enabled"></label>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
                        <button type="button" class="btn btn-primary" @click="updateProduct"></button>
                    </div>
                </div>
            </div>
        </div>
        <!-- Modal -->
        <div class="modal fade" id="delProductModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content border-0">
                    <div class="modal-header bg-danger text-white">
                        <h5 class="modal-title" id="exampleModalLabel">
                            <span></span>
                        </h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div class="modal-body">
                         <strong class="text-danger">{{ tempProduct.title }}</strong> ()
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
                        <button type="button" class="btn btn-danger" @click="delProduct"></button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import $ from 'jquery';  // jquery
import Pagination from '@/views/admin/Pagination';


export default {
    data() {
        return {
            products: [],
            tempProduct: {},  // (id)
            isNew: false,  // false
            isLoading: false,  // truefalse
            status: {
                fileUploading: false,  // loadging
            },
            pagination: {},  // 
        };
    },
    components:{
        Pagination,
    },
    methods: {
        getProducts(page = 1) {  // productsproducts  // ES6
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/products?page=${page}`;
            const vm = this;  // httpvm
            vm.isLoading = true;  // getProducts
            this.$http.get(api).then((response) => {
                vm.isLoading = false;  // getProducts
                vm.products = response.data.products;
                vm.pagination = response.data.pagination;
            });
        },
        openModal(isNew , item) {
            if(isNew) {
                this.tempProduct = {};
                this.isNew = true;
            }else {
                this.tempProduct = Object.assign({} , item);  // item{}item
                this.tempProduct = item;
                this.isNew = false;
            }
            $('#productModal').modal('show');
        },
        updateProduct() {
            let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product`;
            let httpMethod = 'post';
            console.log(api);
            const vm = this;  // vm = data
            if (!vm.isNew) {  // vmAPI
                api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;  // id
                httpMethod = 'put'  // httpMethod=postput
            }
            this.$http[httpMethod](api , { data:vm.tempProduct }).then((response) => {  // vm.tempProduct{}data:
                console.log(response.data);
                if (response.data.success) {
                    $('#productModal').modal('hide');
                    vm.getProducts();
                }else {
                    $('#productModal').modal('hide');
                    vm.getProducts();
                    console.log('');
                }
            });        
        },
        delModal(item) {
            $('#delProductModal').modal('show');
            this.tempProduct = item;
        },
        delProduct() {
            const vm = this;
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
            this.$http.delete(api).then((response) => {  
                console.log(response.data);
                if (response.data.success) {  // updateProductgetProducts
                    $('#delProductModal').modal('hide');
                    vm.getProducts();
                }else {  // console.log('')
                    $('#delProductModal').modal('hide');
                    vm.getProducts();
                    console.log('');
                }
            });
        },
        uploadFile() {
            console.log(this);
            const uploadedFile = this.$refs.files.files[0];  // filesfiles
            const vm = this;
            const formData = new FormData();   // 
            formData.append('file-to-upload' , uploadedFile);
            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/upload`;
            vm.status.fileUploading = true;  // true
            this.$http.post(url , formData , {  // formData
                headers: {
                    'Content-Type': 'multipart/form-data'  // formdata
                },
            }).then((response) => {
                console.log(response.data);
                vm.status.fileUploading = false;  // false
                if(response.data.success) {  // 
                    // vm.tempProduct.imageUrl = response.data.imageUrl;  // tempProduct.imageUrl
                    // console.log(vm.tempProduct);
                    vm.$set(vm.tempProduct , 'imageUrl' , response.data.imageUrl)  // set
                }else {
                    this.$bus.$emit('message:push' , response.data.message , 'danger');  // $emit(,,)
                }
            });
        },
    },
    created() {  // 
        this.getProducts();
    },
};

</script>




