import Vue from 'vue';
import VueRouter from 'vue-router';
// 

// import Home from '@/components/HelloWorld';
// ()
import MainPage from '@/views/front/MainPage';  // 
import Shopping from'@/views/front/Shopping';  // 

import ProductList from '@/components/pages/front/ProductList';  // 
import ProductDetail from '@/components/pages/front/ProductDetail';  // 
import CartList from '@/components/pages/front/CartList';  // 
import OrderPayment from '@/components/pages/front/OrderPayment';  // 
import OrderInfo from '@/components/pages/front/OrderInfo';  // 


// ()
import Dashboard from '@/views/admin/Dashboard';  // 
import Login from '@/components/pages/admin/Login';  // 
import Products from '@/components/pages/admin/Products';  // 
import OrdersList from '@/components/pages/admin/OrdersList';  // 
import Coupons from '@/components/pages/admin/Coupons';  // 



Vue.use(VueRouter);


const routes = [
        // 
        {
            path: '*',  // 
            redirect: '/',  
        },

        {
            name: 'MainPage',
            path: '/',
            component: MainPage,
        },

        {  
            name: 'Shopping',
            path: '/shopping',
            component: Shopping,
            children: [
                {   // /shopping/shopping_prod
                    name: 'ProductList',
                    path: 'productlist',
                    component: ProductList,  
                },
                {   // /shopping/ProductDetail/:product_id
                    name: 'ProductDetail',
                    path: 'productdetail/:product_id',
                    component: ProductDetail,  
                },
                {  // /shopping/cartlist
                    name: 'CartList',
                    path: 'cartlist',
                    component: CartList,  
                },
                {  // /shopping/OrderPayment
                    name: 'OrderPayment',
                    path: 'OrderPayment',
                    component: OrderPayment,  
                },
                {  // /shopping/OrderPayment
                    name: 'OrderInfo',
                    path: 'OrderInfo/:order_id',
                    component: OrderInfo,  
                },
            ],
        },

        // 
        // {
        //     path: '*',  // 
        //     redirect: '/',  
        // },

        {
            name: 'Login',
            path: '/login',
            component: Login,
        },

        {
            name: 'Dashboard',
            path: '/admin',
            component: Dashboard,
            children: [
                {
                    name: 'Products',
                    path: 'products',
                    component: Products,
                    meta: { requiresAuth: true },  // 
                },
                {
                    name: 'OrdersList',
                    path: 'orderslist',
                    component: OrdersList,
                    meta: { requiresAuth: true },  // 
                },
                {
                    name: 'Coupons',
                    path: 'coupons',
                    component: Coupons,
                    meta: { requiresAuth: true },  // 
                },
            ],
        },
    ];

    const router = new VueRouter({
        routes,
        scrollBehavior(to, from, savedPosition) {
            if (savedPosition) {
                return savedPosition;
            }
            return { x: 0, y: 0 };
        },
    });


export default router;




