<template>
    <div>
        <loading :active.sync="isLoading"></loading>
        <div class="text-right">
            <button class="btn btn-primary mt-4" @click="openModal(true)"></button>
        </div>
        <table class="table mt-4">
            <thead>
                <th width="120"></th>
                <th width="120"></th>
                <th width="100">(%)</th>
                <th width="100"></th>
                <th width="100" class="text-center"></th>
                <th width="100" class="text-center"></th>
            </thead>
            <tbody>
                <tr v-for="(item) in coupons" :key="item.id">
                    <td>{{ item.title }}</td>
                    <td>{{ item.code }}</td>
                    <td>{{ item.percent }}%</td>
                    <td>{{ item.due_date | date }}</td>
                    <td class="text-center">
                        <span v-if="item.is_enabled" class="text-success"></span>
                        <span v-else></span>
                    </td>
                    <td class="text-center">
                        <button class="btn btn-outline-primary btn-sm" @click="openModal(false , item)"></button>
                        <button class="btn btn-outline-danger btn-sm" @click="delModal(item)"></button>
                    </td>
                </tr>
            </tbody>
        </table>
        <!--  -->
        <Pagination :pagination="pagination" @emitpage="getCoupons"></Pagination>
        <!-- / Modal -->
        <div class="modal fade" id="couponModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog modal-lg" role="document">
                <div class="modal-content border-0">
                    <div class="modal-header bg-dark text-white">
                        <h5 class="modal-title" id="exampleModalLabel">
                            <span></span>
                        </h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div class="modal-body">
                        <div class="row">
                            <div class="col-sm-4">
                                <div class="form-group">
                                    <label for="title"></label>
                                    <input type="text" class="form-control" id="title" v-model="tempCoupon.title" placeholder="">
                                </div>
                            </div>
                            <div class="col-sm-8">
                                <div class="form-group">
                                    <label for="code"></label>
                                    <input type="text" class="form-control" id="code" v-model="tempCoupon.code" placeholder="">
                                </div>

                                <div class="form-row">
                                    <div class="form-group col-md-6">
                                        <label for="dueDate"></label>
                                        <input type="text" class="form-control" id="dueDate" v-model="tempCoupon.due_date" placeholder="">
                                    </div>
                                    <div class="form-group col-md-6">
                                        <label for="percent"></label>
                                        <input type="unit" class="form-control" id="percent" v-model="tempCoupon.percent" placeholder="">
                                    </div>
                                </div>
                                <div class="form-group">
                                    <div class="form-check">
                                        <input class="form-check-input" type="checkbox" v-model="tempCoupon.is_enabled" :true-value="1" :false-value="0" id="is_enabled">
                                        <!-- 01:true-value="1" :false-value="0" -->
                                        <label class="form-check-label" for="is_enabled"></label>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
                        <button type="button" class="btn btn-primary" @click="updateCoupons"></button>
                    </div>
                </div>
            </div>
        </div>
        <!-- Modal -->
        <div class="modal fade" id="delCouponModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content border-0">
                    <div class="modal-header bg-danger text-white">
                        <h5 class="modal-title" id="exampleModalLabel">
                            <span></span>
                        </h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div class="modal-body">
                         <strong class="text-danger">{{ tempCoupon.title }}</strong> ()
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
                        <button type="button" class="btn btn-danger" @click="deleteCoupons"></button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import $ from 'jquery';  // jquery
import Pagination from '@/views/admin/Pagination';

export default {
    data() {
        return {
            coupons: [],
            tempCoupon: {},  // (id)
            isNew: false,  // false
            isLoading: false,  // truefalse
            status: {
                fileUploading: false,  // loadging
            },
            pagination: {},  // 
        };
    },
    components:{
        Pagination,
    },
    methods: {
        getCoupons(page = 1) {  // productsproducts  // ES6
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupons?page=${page}`;
            const vm = this;  // httpvm
            vm.isLoading = true;  // getProducts
            this.$http.get(api).then((response) => {
                // console.log(response.data);
                vm.isLoading = false;  // getProducts
                vm.coupons = response.data.coupons;
                vm.pagination = response.data.pagination;
            });
        },
        openModal(isNew , item) {
            if(isNew) {
                this.tempCoupon = {};
                this.isNew = true;
            }else {
                this.tempCoupon = Object.assign({} , item);  // item{}item
                // this.tempCoupon = item;
                this.isNew = false;
            }
            // timestamp  yyyy-mm-dd 
            this.tempCoupon.due_date = this.timestampConvert(this.tempCoupon.due_date);
            $('#couponModal').modal('show');
        },
        updateCoupons() {
            let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon`;
            let httpMethod = 'post';
            console.log(api);
            const vm = this;  // vm = data
            if (!vm.isNew) {  // vmAPI
                api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`;  // id
                httpMethod = 'put'  // httpMethod=postput
            }
            //  timestamp
            vm.tempCoupon.due_date = Math.floor(new Date(vm.tempCoupon.due_date) / 1000);

            this.$http[httpMethod](api , { data:vm.tempCoupon }).then((response) => {  // vm.tempCoupon {}data:
                console.log(response.data);
                if (response.data.success) {
                    $('#couponModal').modal('hide');
                    vm.getCoupons();
                }else {
                    $('#couponModal').modal('hide');
                    vm.getCoupons();
                    console.log('');
                }
            });        
        },
        // 
        delModal(item) {
            $('#delCouponModal').modal('show');
            this.tempCoupon = item;
        },
        deleteCoupons() {
            const vm = this;
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`;
            this.$http.delete(api).then((response) => {  
                console.log(response.data);
                if (response.data.success) {  // updateProductgetProducts
                    $('#delCouponModal').modal('hide');
                    vm.getProducts();
                }else {  // console.log('')
                    $('#delCouponModal').modal('hide');
                    vm.getProducts();
                    console.log('');
                }
            });
        },
        //  (YYYY-MM-DD)
        timestampConvert(time) {
            if (time) {
                const date = new Date(time * 1000);
                const formatDate = (num) => {
                if (num < 10) {
                    return `0${num}`;
                }
                return num;
            };
            // 
            const [YYYY, MM, DD] = [
                date.getFullYear(),
                formatDate(date.getMonth() + 1),
                formatDate(date.getDate()),
            ];
                // ES6 template strings
                return `${YYYY}-${MM}-${DD}`;
            }
            return '';
        },
    },
    created() {  // 
        this.getCoupons();
    },
};

</script>




