<template>
    <div class="orderInfo">
        <loading :active.sync="isLoading"></loading>
        <div class="container">
            <!--  -->
            <div class="row text-center p-5">
                <div class="col-md-3 alert alert-sborange alert-rounded">
                    <i class="fas fa-check-circle"></i>
                    
                    <i class="fas fa-angle-double-right"></i>
                </div>
                <div class="col-md-3 alert alert-sborange alert-rounded">
                    <i class="fas fa-check-circle"></i>
                    
                    <i class="fas fa-angle-double-right"></i>
                </div>
                <div class="col-md-3 alert alert-sborange alert-rounded" v-if="!order.is_paid">
                    
                    <i class="fas fa-angle-double-right"></i>
                </div>
                <div class="col-md-3 alert alert-sborange alert-rounded" v-else>
                    <i class="fas fa-check-circle"></i>
                    
                    <i class="fas fa-angle-double-right"></i>
                </div>
                <div class="col-md-3 alert alert-sbtan alert-rounded" v-if="!order.is_paid">
                    
                </div>
                <div class="col-md-3 alert alert-sborange alert-rounded" v-else>
                    <i class="fas fa-check-circle"></i>
                    
                </div>
            </div>

        <div class="container px-5">
            <div class="orderinfo row justify-content-center mt-4">
                <h3 class="text-sbbrown font-weight-bold"></h3>
                <table class="table mt-3 p-5">
                    <thead>
                        <tr>
                            <th></th>
                            <th></th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="item in order.products" :key="item.id">
                            <td>{{ item.product.title }}</td>
                            <td>{{ item.qty }}</td>
                            <td>{{ item.final_total | currency }}</td>
                        </tr>
                    </tbody>
                    <tfoot>
                        <tr>
                            <td class="text-right font-weight-bold h5" colspan="2"></td>
                            <td class="text-danger font-weight-bold h5" colspan="3">{{ order.total | currency }}</td>
                        </tr>
                    </tfoot>                    
                </table>
            </div>
            <div class="userinfo row justify-content-center mt-4">
                <h3 class="text-sbbrown font-weight-bold"></h3>
                <table class="table mt-3 p-5">
                    <tbody>
                        <tr>
                            <td class="title"></td>
                            <td>{{ order.create_at | date }}</td>
                        </tr>
                        <tr>
                            <td class="title"></td>
                            <td>{{ order.user.name }}</td>
                        </tr>
                        <tr>
                            <td class="title"></td>
                            <td>{{ order.user.tel }}</td>
                        </tr>
                        <tr>
                            <td class="title">Email</td>
                            <td>{{ order.user.email }}</td>
                        </tr>
                        <tr>
                            <td class="title"></td>
                            <td>{{ order.user.address }}</td>
                        </tr>
                    </tbody>
                    <tfoot>
                        <tr v-if="order.user.payment === 'CVS'">
                            <td class="title"></td>
                            <td>CLOY011109251982</td>
                        </tr>
                        <tr v-if="order.user.payment === 'ATM'">
                            <td class="title">ATM</td>
                            <td>(822)250510091408011404</td>
                        </tr>
                        <tr>
                            <td class="title"></td>
                            <td class="text-danger font-weight-bold" v-if="!order.is_paid"></td>
                            <td class="text-info" v-else></td>
                        </tr>
                    </tfoot>
                </table>
                
                
            </div>
            <div class="text-right py-5">
                <button type="button" class="btn btn-sborange" v-if="!order.is_paid" @click.prevent="payOrder"></button>
                <!-- <button type="button" class="btn btn-secondary cancel" v-else @click.prevent="$router.push('/shopping/productlist')"></button> -->
            </div>
        </div>
    </div>

    </div>
</template>





<script>
import $ from 'jquery';

export default {
    data() {
        return {
            orderId: '',  // 
            order: {  // 
                user: {},
            },
            isLoading: false, // loading 
        }
    },
    methods: {
        // 
        getOrder() {
            const vm = this;
            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order/${vm.orderId}`;
            vm.$http.get(url).then((response) => {
                if (!response.data.success) {
                    vm.$bus.$emit('message:push', response.data.message, 'danger');
                    vm.$router.push('/productlist');
                } else {
                    vm.order = response.data.order;
                }
            });
        },
        // 
        payOrder() {
            const vm = this;
            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/pay/${vm.orderId}`;
            vm.isLoading = true;
            vm.$http.post(url).then((response) => {
                vm.isLoading = false;
                if (response.data.success) {
                    vm.getOrder();
                    //  Navbar 
                    vm.$bus.$emit('cart:get');
                    // $('#completeModal').modal('show');
                } else {
                    vm.$bus.$emit('message:push', response.data.message, 'danger');
                }
            });
        },
    },
    created() {
        // 
        this.orderId = this.$route.params.order_id;
        this.getOrder();
    },
}

</script>




