<template>
    <div>
        <loading :active.sync="isLoading"></loading>
        <!-- <div class="text-right">
            <button class="btn btn-primary mt-4" @click="openModal(true)"></button>
        </div> -->
        <table class="table mt-5">
            <thead>
                <th width="120" class="text-center"></th>
                <th width="120" class="text-center"></th>
                <th></th>
                <th width="100"></th>
                <th width="150" class="text-center"></th>
                <th width="250" class="text-center"></th>
                <th width="120" class="text-center"></th>
                <th width="150" class="text-center"></th>
            </thead>
            <tbody>
                <tr v-for="(item) in sortOrder" :key="item.id">
                    <td class="text-center">{{ item.create_at | date }}</td>
                    <td class="text-center">{{ item.user.name }}</td>
                    <td>
                        <ul class="list-unstyled">
                            <li v-for="(product) in item.products" :key="product.id">{{ product.product.title }}</li>
                        </ul>
                    </td>
                    <td class="text-center">
                        <ul class="list-unstyled">
                            <li v-for="(product) in item.products" :key="product.id">{{ product.qty }}</li>
                        </ul>
                    </td>
                    <td class="text-right pr-4">
                        {{ item.total | currency }}
                    </td>
                    <td class="text-center">
                        {{ item.message }}
                    </td>
                    <td class="text-center">
                        <span v-if="item.is_paid" class="text-success"></span>
                        <span v-else></span>
                    </td>
                    <td>
                        <button class="btn btn-outline-primary btn-sm" @click="openModal(item)"></button>
                        <button class="btn btn-outline-danger btn-sm" @click="delModal(item)"></button>
                    </td>
                </tr>
            </tbody>
        </table>
        <!--  -->
        <Pagination :pagination="pagination" @emitpage="getOrders"></Pagination>
        <!-- Modal -->
        <div class="modal fade" id="orderModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog modal-lg" role="document">
                <div class="modal-content border-0">
                    <div class="modal-header bg-dark text-white">
                        <h5 class="modal-title" id="exampleModalLabel">
                            <span></span>
                        </h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close" @click='getOrders'>
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div class="modal-body p-4">
                        <div class="form-row">
                            <div class="col-md-6">
                                <div class="form-group">
                                    <label for="create_at"></label>
                                    <div>{{ tempOrder.id }}</div>
                                </div>
                            </div>
                            <div class="col-md-3">
                                <div class="form-group">
                                    <label for="create_at"></label>
                                    <div>{{ tempOrder.create_at | date }}</div>
                                </div>
                            </div>
                            <div class="col-md-3">
                                <div class="form-group">
                                    <label for="is_paid"></label>
                                    <div class="text-success" v-if="tempOrder.is_paid">
                                        <span>{{ tempOrder.paid_date | date }} | </span>
                                    </div>
                                    <div class="text-success" v-else>
                                        <span></span>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <hr>
                        <div class="form-row">
                            <div class="col-md-6">
                                    <label for="is_paid"></label>
                                    <input type="text" class="form-control" name="name" id="username" v-model="tempOrder.user.name">
                            </div>
                            <div class="col-md-6">
                                <label for="tel"></label>
                                <input type="tel" class="form-control" name="tel" id="usertel" v-model="tempOrder.user.tel" placeholder="">
                            </div>
                            <div class="col-md-6">
                                <label for="email">Email</label>
                                <input type="email" class="form-control" name="email" id="useremail" v-model="tempOrder.user.email" placeholder="">
                            </div>
                            <div class="col-md-6">
                                <label for="address"></label>
                                <input type="address" class="form-control" name="address" id="useraddress" v-model="tempOrder.user.address" placeholder="">
                            </div>
                            <div class="col-md">
                                <label for="message"></label>
                                <textarea class="form-control" rows="3" name="message" id="usermessage" v-model="tempOrder.message" placeholder=""></textarea>
                                <!-- <input type="textarea" class="form-control" name="message" id="usermessage" v-model="tempOrder.message" placeholder=""> -->
                            </div>
                        </div>
                        <div class="table-responsive tableDetail">
                            <table class="table table-hover my-2">
                                <thead>
                                    <tr>
                                        <th width="40"></th>
                                        <th></th>
                                        <th class="text-center"></th>
                                        <!-- <th></th> -->
                                        <th class="text-center"></th>
                                        <th class="text-center"></th>
                                        <th class="text-center"></th>
                                        <th class="text-center"></th>
                                        
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr v-for="(item) in tempOrder.products" :key="item.id">
                                        <td ><i class="fas fa-trash-alt"></i></td>
                                        <td>{{ item.product.title }}</td>
                                        <td class="text-center">{{ item.qty }}</td>
                                        <!-- <td>{{ item.product.unit }}</td> -->
                                        <td class="text-right">{{ item.product.price | currency }}</td>
                                        <td class="text-right">{{ item.total | currency }}</td>
                                        <td class="text-center" v-if="item.hasOwnProperty('coupon')">{{ item.coupon.percent }}%</td>
                                        <td class="text-center" v-else></td>
                                        <td class="text-right">{{ item.final_total | currency }}</td>
                                        
                                    </tr>
                                    <tr>
                                        <td class="text-right" colspan="6"></td>
                                        <td class="text-right" colspan="7">{{ tempOrder.total | currency }}</td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                    <div class="modal-footer p-4">
                        <button type="button" class="btn btn-outline-secondary" data-dismiss="modal" @click="getOrders"></button>
                        <button type="button" class="btn btn-primary" @click="updateOrder"></button>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
import $ from 'jquery';  // jquery
import Pagination from '@/views/admin/Pagination';

export default {
    data() {
        return {
            orders: [],  // 
            tempOrder: {  // 
                create_at: '',
                is_paid: '',
                message: '',
                paid_date: '',
                products: [],
                total: '',
                user: {
                    address: '',
                    email: '',
                    name: '',
                    tel: '',
                },
            },  
            isLoading: false,  // truefalse
            pagination: {},  // 
        };
    },
    components:{
        Pagination,
    },
    methods: {
        getOrders(page = 1) {  // productsproducts  // ES6
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/orders?page=${page}`;
            const vm = this;  // httpvm
            vm.isLoading = true;  // getProducts
            this.$http.get(api).then((response) => {
                vm.isLoading = false;  // getProducts
                vm.orders = response.data.orders;
                vm.pagination = response.data.pagination;
            });
        },
        openModal(item) {
            this.tempOrder = Object.assign({} , item);  // item{}item
            $('#orderModal').modal('show');
        },
        updateOrder() {
            const vm = this;  // vm = data
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/order/${vm.tempOrder.id}`;
            
            this.$http.put(api, { data: vm.tempOrder }).then((response) => {  // vm.tempOrder{}data:
                console.log(response.data);
                if (response.data.success) {
                    $('#orderModal').modal('hide');
                    vm.getOrders();
                }else {
                    $('#orderModal').modal('hide');
                    vm.getOrders();
                    console.log('');
                }
            });        
        },
        // delModal(item) {
        //     $('#delOrderModal').modal('show');
        //     this.tempOrder = item;
        // },
        // delOrder() {
        //     const vm = this;
        //     const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/order/${vm.tempOrder.id}`;
        //     this.$http.delete(api).then((response) => {  
        //         console.log(response.data);
        //         if (response.data.success) {  // updateProductgetOrders
        //             $('#delOrderModal').modal('hide');
        //             vm.getOrders();
        //         }else {  // console.log('')
        //             $('#delOrderModal').modal('hide');
        //             vm.getOrders();
        //             console.log('');
        //         }
        //     });
        // },
    },
    computed: {
        sortOrder() {
            const vm = this;
            let newOrder = [];
            if (vm.orders.length) {
                newOrder = vm.orders.sort((a, b) => {
                const aIsPaid = a.is_paid ? 1 : 0;
                const bIsPaid = b.is_paid ? 1 : 0;
                return bIsPaid - aIsPaid;
                });
            }
            return newOrder;
        },
    },
    created() {  // 
        this.getOrders();
    },
};

</script>




